/* NavBar.css */
.navbar {
    background-color: #f8f9fa;
    padding: 10px;
  }
  
  .navbar ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    gap: 10px;
  }
  
  .navbar li {
    display: inline;
  }
  
  .navbar a {
    text-decoration: none;
    color: #007bff;
  }
  
  .navbar a.active {
    font-weight: bold;
    color: #0056b3;
  }
  